<template>
  <v-snackbar
    :timeout="10000"
    v-model="confirm"
    top
    multi-line
    persistent
    color="error"
    dark
    icon="mdi-alert-circle-outline"
    transition="scale-transition"
    >  ?
    <v-btn color="error" @click="confirmYes" min-width="80"></v-btn>
    <v-btn color="error" @click="confirmNo" min-width="80"></v-btn>
  </v-snackbar>
</template>
<script>
import axios from "axios";
import {EventBus} from "./eventBus.js";

export default {
  data() {
    return {
      alertText: "",
      confirm: false,
      mediaId:0,
    };
  },
  mounted() {
    EventBus.$on("mediaTrash", item => {
      console.log('')
      this.mediaId = item.id
      this.confirm = true;
    });
  },
  methods: {
    confirmYes() {
      axios
        .put("/api/media/trash", {
          data: { id: this.mediaId }
        })
        .then(r => {
          location.href="/media"
        })
        .catch(e => {
          console.error(e.message);
        });
    },
    confirmNo() {
      this.confirm = false;
    }
  }
};
</script>
