<template>
  <v-form ref="form" v-model="valid">
    <v-dialog v-model="dialog" persistent scrollable max-width="600px">
      <AlertSnackBar></AlertSnackBar>
      <v-card>
        <v-app-bar dark color="blue-grey">
          <v-card-title>
            <span> </span>
          </v-card-title>
          <v-spacer></v-spacer>
          <v-btn icon dark>
            <v-icon @click="modalClose" size="30px">mdi-close</v-icon>
          </v-btn>
        </v-app-bar>
        <v-card-text>
          <v-divider></v-divider>

          <!-- text -->
          <!-- <v-btn-toggle
                      v-model="text"
                      v-if="this.auth"
                      color="pink"
                      style="background-color:white"
                    >
                    </v-btn-toggle> -->
          <!-- text -->

          <v-col cols="20">
            <div class="colupload">
              <v-text-field
                v-model="name"
                :counter="20"
                :rules="nameRules"
                label=" *"
                prepend-inner-icon="mdi-arrow-right-drop-circle"
                required
              ></v-text-field>
            </div>
          </v-col>
          <v-col cols="12">
            <div class="colupload">
              <v-menu
                v-model="menu"
                :close-on-content-click="false"
                :nudge-right="10"
                transition="scale-transition"
                offset-y
                min-width="290px"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field
                    v-model="date"
                    label=""
                    :rules="dateRules"
                    :readonly="readonly"
                    prepend-inner-icon="mdi-calendar"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  locale="ko"
                  v-model="date"
                  @input="menu = false"
                ></v-date-picker>
              </v-menu>
            </div>
          </v-col>
          <v-col cols="12">
            <div class="colupload">
              <v-textarea
                style="min-height: 10rem;"
                v-model="detail"
                outlined
                :counter="200"
                :rules="detailRules"
                label="*"
                prepend-inner-icon="mdi-message-bulleted"
                required
              ></v-textarea>
               3D      ,   
              .
            </div>
          </v-col>

          <v-col cols="12">
            <div class="colupload">
              <v-file-input
                v-model="file"
                accept="image/*, video/*, audio/*,application/pdf"
                placeholder=" "
                prepend-inner-icon="mdi-file"
                label=""
                value="filevalue"
              >
                <template v-slot:selection="{ index, text }">
                  <!-- <v-chip
                      v-if="index < 3"
                      color="deep-purple accent-4"
                      dark
                      label
                      small
                    >  -->
                  {{ text }}
                  <!-- </v-chip> -->
                </template>
              </v-file-input>
                    
            </div>
          </v-col>

          <v-col cols="12">
            <div>
              <v-icon>mdi-view-grid</v-icon>

              <strong color="black">  *</strong>

              <!-- information icon   menu   -->

              <v-menu
                v-model="menus"
                :close-on-content-click="false"
                :nudge-width="200"
                offset-x
              >
                <template v-slot:activator="{ on }">
                  <v-btn color="indigo" dark icon v-on="on">
                    <v-icon color="#6c98c3">mdi-information</v-icon>
                  </v-btn>
                </template>
                <v-card>
                  <v-list>
                    <v-list-item>
                      <img
                        width="100%"
                        height="200rem"
                        src="@/assets/mediaexample.jpg"
                      />
                    </v-list-item>
                  </v-list>
                </v-card>
                <!--  -->
              </v-menu>
            </div>
            <!-- information icon   menu   -->
            <v-select
              v-model="width"
              label=""
              :rules="widthRules"
              :items="number"
              :readonly="readonly"
              prepend-inner-icon="mdi-table-column-width"
            >
            </v-select>
            &nbsp;
            <strong>x</strong>
            &nbsp;
            <v-select
              v-model="height"
              label=""
              numberOnly
              :items="number"
              :rules="heightRules"
              prepend-inner-icon="mdi-table-row-height"
            ></v-select>
            <!-- <div>
            
            </div> -->
            <!-- <v-select
         v-model="actselect"
        :items="actitems"
         prepend-inner-icon="mdi-view-grid"
         label="  *"
         >
      </v-select> -->
          </v-col>
          <v-spacer></v-spacer>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" @click="saveClick">
            
          </v-btn>
          <v-btn color="error" @click="modalClose">
            
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-form>
</template>

<script>
//   eventbus
import { EventBus } from "./eventBus";
import axios from "axios";
import AlertSnackBar from "./AlertSnackBar.vue";
export default {
  components: {
    AlertSnackBar
  },
  data: () => ({
    date: new Date().toISOString().substr(0, 10),
    menu: false,
    menus: false,
    dialog: false,
    valid: true,
    select: null,
    actselect: null,
    readonly: false,
    file: [],
    date: "",
    name: "",
    width: "",
    height: "",
    nameRules: [
      v => !!v || "  ",
      v => (v && v.length <= 20) || "20    ."
    ],

    detail: "",
    detailRules: [
      v => !!v || " ",
      v => (v && v.length <= 200) || "200    ."
    ],
    dateRules: [v => !!v || " "],
    widthRules: [v => !!v || " "],
    heightRules: [v => !!v || " "],

    actitems: ["", "", "", "", ""],
    number: ["1", "2", "3", "4", "5", "6", "7", "8", "9"]
  }),
  mounted() {
    EventBus.$on("mdrequest", item => {
      this.dialog = true;
    });
  },

  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        this.snackbar = true;
      }
    },

    modalClose() {
      this.dialog = false;
      this.$refs.form.reset();
      this.$refs.form.resetValidation();
    },
    saveClick() {
      console.log(this.date + "date");
      console.log(this.detail + "detail");
      console.log(this.name + "*edt");
      console.log(this.name + "*edt");
      console.log(this.width + "*");
      console.log(this.height + "*");
      console.log(this.file + "*");
      console.log(this.text + "*");
      EventBus.$emit("SaveItem", "Media");
    }
  }
};
</script>
<style>
.colupload {
  color: black;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  height: auto;
  -webkit-box-flex: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  max-width: 30rem;
  min-width: 30rem;
  width: 100%;
}

/* .theme--light.v-select .v-select__selections {
    color: rgba(0, 0, 0, 0.87);
    min-width: 2rem;
    max-width: 7rem; */
/* } */
</style>
<style scoped>
.v-select {
  color: rgba(0, 0, 0, 0.87);
  min-width: 3rem;
  max-width: 12rem;
}
</style>
