<template>
  <v-dialog v-model="dialog" scrollable max-width="400px" persistent>
    <AlertSnackBar></AlertSnackBar>
    <v-card>
      <!-- <mediainfoEdit></mediainfoEdit>   -->
      <v-app-bar dark color="blue">
        <v-card-title>
          <span>{{ editname }}</span>
        </v-card-title>
        <v-spacer></v-spacer>
        <v-btn @click="modalClose()" icon dark>
          <v-icon size="30px">mdi-close</v-icon>
        </v-btn>
      </v-app-bar>

      <v-card-text class="pa-0">
        <!-- isstatuson    -->
        
        <!-- isstatuson    -->
        <!--  source src, option   -->

        <!-- <my-video play :sources="video.sources" :options="video.options" controls width="500px"></my-video>
            
             -->
        <div id="message"  class="video-backgroud"></div>
        <!-- <input type="file" accept="video/*"/>
<my-video controls autoplay></my-video> -->
        <video
          id="video"
          controls="controls"
          class="videoheight"
          width="100%"
          height="auto"
          autoplay
          loop
        >
          <source
            :src="require(`../../../../upload/media/${video}`)"
            type="video/mp4"
          />
        </video>

        <!-- <my-video width="320" height="240" controls>
  <source src="ball.mp4" type="video/mp4"> 
  Your browser does not support the video tag.
</my-video> -->

        <v-divider></v-divider>
        <v-card>
          <v-list>
            <v-list-item>
              <v-row justify="center">
                <v-btn icon @click="mediaInfoEdit2()">
                  <v-icon color="indigo">mdi-square-edit-outline</v-icon>
                </v-btn>
                 &nbsp;
                <!-- html5  download -->
                <a href="../test.mp4" download>
                  <v-btn icon>
                    <v-icon color="indigo">mdi-arrow-collapse-down</v-icon>
                  </v-btn>
                </a>
                
              </v-row>
            </v-list-item>
          </v-list>
        </v-card>
        <v-list>
          <!--     readonly, outline  read    -->
          <div v-if="isStatusOn">
            <v-list-item>
              <v-list-item-icon>
                <v-icon color="indigo">mdi-account-check</v-icon>
              </v-list-item-icon>

              <v-list-item-content>
                <v-list-item-subtitle></v-list-item-subtitle>
                <v-text-field
                  dense
                  solo
                  v-model="editname"
                  label="*"
                  required
                  :readonly="read"
                  :outlined="editout"
                >
                </v-text-field>
              </v-list-item-content>
            </v-list-item>
          </div>
          <v-list-item>
            <v-list-item-icon>
              <v-icon color="indigo">mdi-alpha-s-circle</v-icon>
            </v-list-item-icon>
            <v-list-item-content class="allshow">
              <!-- <div class="allshow">
                <v-list-item-subtitle></v-list-item-subtitle>
                <span>
                  {{ Math.floor(editsize / (1024 * 1024)) / 10 + "MB" }}</span>
              </div> -->
              <div class="allshow">
                <v-list-item-subtitle></v-list-item-subtitle>
                <span>{{ Math.floor(editsize / (1024 * 1024)) / 10 + "MB" }}</span>
              </div>
            </v-list-item-content>
          </v-list-item>
          <v-list-item>
            <v-list-item-icon>
              <v-icon color="indigo">mdi-calendar</v-icon>
            </v-list-item-icon>
            <v-list-item-content class="allshow">
              <div class="allshow">
                <v-list-item-subtitle></v-list-item-subtitle>
                <span>{{ editadded | formatDateYMD }}</span>
              </div>
            </v-list-item-content>
          </v-list-item>
          <v-list-item>
            <v-list-item-icon>
              <v-icon color="indigo">mdi-play-circle</v-icon>
            </v-list-item-icon>
            <v-list-item-content class="allshow">
              <div class="allshow">
                <v-list-item-subtitle></v-list-item-subtitle>
                <span>{{ editduration }}</span>
              </div>
            </v-list-item-content>
          </v-list-item>
          <v-list-item>
            <v-list-item-icon>
              <v-icon color="indigo">mdi-view-grid</v-icon>
            </v-list-item-icon>
            <v-list-item-content class="allshow">
              <div class="allshow">
                <div v-if="isStatusOn">
                  <v-col cols="20">
                    <div class="colupload">
                      <!--  -->
                      <v-combobox
                        v-model="mediacategory"
                        :items="mediaselect"
                        :search-input.sync="search"
                        hide-selected
                        hint=" 5 "
                        label=" "
                        multiple
                        persistent-hint
                        chips
                      >
                        <template v-slot:no-data>
                          <v-list-item>
                            <v-list-item-content>
                              <v-list-item-title>
                                "<strong>{{ search }}</strong
                                >"   <kbd>enter</kbd>d
                                .
                              </v-list-item-title>
                            </v-list-item-content>
                          </v-list-item>
                        </template>
                      </v-combobox>
                    </div>
                  </v-col>
                </div>
                <div v-else>
                  <v-list-item-subtitle></v-list-item-subtitle>
                  <v-col>
                    <v-chip
                      color="primary"
                      v-for="item in editcategory"
                      :key="item"
                    >
                      <span class="v-chip__content">
                        {{ item }}
                      </span>
                      &nbsp;
                    </v-chip>
                  </v-col>
                </div>
              </div>
            </v-list-item-content>
          </v-list-item>
          <v-list-item>
            <v-list-item-icon>
              <v-icon color="indigo">mdi-message-bulleted</v-icon>
            </v-list-item-icon>
            <v-list-item-content class="allshow">
              <div class="allshow">
                <v-list-item-subtitle></v-list-item-subtitle>
                <!-- readonly, outline  read             -->
                <v-textarea
                  no-resize
                  class="textheight"
                  v-model="editdetail"
                  dense
                  solo
                  required
                  :readonly="read"
                  :outlined="editout"
                ></v-textarea>
              </div>
            </v-list-item-content>
          </v-list-item>
        </v-list>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <div v-if="isStatusOn">
          <v-btn color="primary" v-model="savebtn" @click="saveClick">
            
          </v-btn>
          <v-btn color="error" @click="mediaInfocancel()" v-model="closebtn">
            
          </v-btn>
        </div>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import AlertSnackBar from "./AlertSnackBar.vue";
import axios from "axios";
import { EventBus } from "./eventBus";
import myVideo from "vue-video";
// import mediainfoEdit from '@/components/media/mediainfoEdit.vue'
export default {
  data() {
    return {
      e6: [],
      states: ["", "", "", "", ""],
      //a:'../../../../upload/media/Fireworks - 5269.mp4',
      //a:"@/assets/movie.mp4",
      a: ["http://techslides.com/demos/sample-videos/small.mp4"],
      video: "",
      a: "@/assets/videoex.mp4",
      editname: "",
      tag: [],
      mediaId: 0,
      editcategory: [],
      editsize: 0,
      editadded: "",
      editduration: 0,
      editdetail: "",
      formTitle: "",

      dialog: false,
      //readonly:true,
      read: true,
      editout: false,
      editsolo: true,
      savebtn: false,
      isStatusOn: false,
      feelgood: "@/assets/movie.mp4",

      mediaselect: ["", "", "", "", "", "", ""],
      mediacategory: [],
      video: {
        sources: [
          {
            //src: 'http://techslides.com/demos/sample-videos/small.mp4',
            src: "videoex.mp4",
            type: "video/mp4"
          }
        ],
        options: {
          autoplay: true,
          volume: 0.6,
          poster: "@/assets/korea.png"
        }
      }
    };
  },

  components: {
    myVideo,
    AlertSnackBar
  },
  methods: {
    saveClick() {
      axios
        .put("/api/media", {
          id: this.mediaId,
          name: this.editname,
          descript: this.editdetail,
          tags: this.mediacategory
        })
        .then(r => {
          EventBus.$emit("SaveItem", "Media");
          //location.href = "/media";
        })
        .catch(e => {
          console.error(e.message);
        });
    },
    removechip(item) {
      this.chips.splice(this.chips.indexOf(item), 1);
      this.chips = [...this.chips];
    },
    remove(item) {
      this.selected.splice(this.selected.indexOf(item), 1);
    },

    modalClose() {
      (this.editname = ""), (this.editsize = 0), (this.editadded = "");
      this.editresolution = "";
      this.category = "";
      this.editDetail = "";
      this.mediacategory = [];
      this.read = true;
      this.editsolo = false;
      this.editout = false;
      this.dialog = false;
      this.isStatusOn = false;
      this.editcategory = [];
    },
    mediaInfoEdit2() {
      this.read = false;
      this.editsolo = true;
      this.editout = true;
      this.isStatusOn = true;
    },
    mediaInfocancel() {
      this.read = true;
      this.editsolo = false;
      this.editout = false;
      this.isStatusOn = false;
      this.editcategory = [];
    }
  },
  computed: {
    videoElement() {
      return this.$refs.video;
    }
  },
  mounted() {
    EventBus.$on("mediainfo", item => {
      
      axios.post(`/api/media/info`, { id: item }).then(r => {
        if (!r.data.success) return console.error(r.data.msg);
        if (r.data.success) {

          this.mediaId = r.data.media.id;
          this.editsize = r.data.media.file.size;
          this.editname = r.data.media.name;
          this.editdetail = r.data.media.description;
          this.editadded = r.data.media.createdAt;
          this.editduration=r.data.media.playTime;
          for (item of r.data.media.tags) {
            this.editcategory.push(item.name);
          }

          for (item of r.data.media.tags) {
            this.mediacategory.push(item.name);
          }
          this.video = r.data.media.file.orgName;
          var video = document.getElementById("video");
          video.load();
          video.play();
        }
      });

      this.dialog = true;
    });

    //    on  mdinfo 
    EventBus.$on("mdedit", item => {
      axios.post(`/api/media/info`, { id: item }).then(r => {
        if (!r.data.success) return console.error(r.data.msg);
        if (r.data.success) {
          this.mediaId = r.data.media.id;
          this.editsize = r.data.media.file.size;
          this.editname = r.data.media.name;
          this.editdetail = r.data.media.description;
          this.editadded = r.data.media.createdAt;
          this.editduration=r.data.media.playTime;
          for (item of r.data.media.tags) {
            this.editcategory.push(item.name);
          }

          for (item of r.data.media.tags) {
            this.mediacategory.push(item.name);
          }
          this.video = r.data.media.file.orgName;
          var video = document.getElementById("video");
          video.load();
          video.play();
        }
      });

      this.read = false;
      this.editsolo = true;
      this.editout = true;
      this.isStatusOn = true;
      this.dialog = true;
    });
  },
  beforeDestroy() {
    EventBus.$off("mdinfo");
  },

  watch: {
    mediacategory(val) {
      if (val.length > 5) {
        this.$nextTick(() => this.mediacategory.pop());
      }
    }
  }
};
</script>
<style scoped>
.video-background {
    z-index: 99999;
}
.v-text-field.v-text-field--enclosed .v-text-field__details {
  margin-bottom: 0px;
}
.videoheight {
  max-height: 340px;
}
a:link {
  text-decoration: none;
  color: black;
}
.allshow {
  -webkit-box-flex: 1;
  -ms-flex: 1 1 100%;
  flex: 1 1 100%;

  text-overflow: clip;
  white-space: pre-wrap;
}
.v-list-item__title {
  -webkit-box-flex: 1;
  -ms-flex: 1 1 100%;
  flex: 1 1 100%;
  overflow: hidden;
  text-overflow: none;
  white-space: none;
}

.videoinput {
}
</style>
<style scoped>
.v-select.v-select--chips .v-select__selections {
  min-height: 42px;
  max-width: 3rem;
}
.v-select.v-select--chips .v-select__selections {
  min-height: 42px;
  max-width: 3rem;
}
.videoheight {
  max-height: 340px;
}
.textheight {
  max-height: 140px;
  resize: none;
}
.v-chip__content {
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  height: 100%;
  max-width: 100%;
  margin-left: 4%;
  /* text-align: center; */
}
</style>
