<template>
  <v-snackbar
    :timeout="false"
    v-model="alert"
    top
    persistent
    multi-line
    color="blue lighten-2"
    dark
    icon="mdi-alert-circle-outline"
    transition="scale-transition"
    >
    <v-btn flat color="blue lighten-2" @click="alertConfirm"></v-btn>
  </v-snackbar>
</template>
<script>
//import axios from 'axios'
import { EventBus } from "./eventBus";
export default {
  data() {
    return {
      alert: false,
      access: false,
      message: ""
    };
  },
  mounted() {
    EventBus.$on("SaveItem", (item, r) => {
      this.alert = true;
      
    });
    
  },

  methods: {
    alertConfirm() {
      if (this.access == false) location.href = "/Media";
      if (this.access == true) location.href = "/Media";
    }
  }
};
</script>
